<template>
    <yd-layout>
        <div class="totalInfo" v-if="firstFlag" v-show="addFlag">
            <div class="tree-menu">
                <trs-tree :type="type" v-on:callbackTop="addFocus" @click="addFlag = !addFlag"></trs-tree>
            </div>
            <!-- <div class="submit" @click.stop.prevent="treeSure()">确定</div> -->
        </div>
        <yd-tabbar slot="tabbar" bgcolor="#fff" v-show="addFlag">
            <yd-button v-on:click.native="treeSure()" size="large" type="primary" bgcolor="#00B6FC" color="#fefefe">确定</yd-button>
        </yd-tabbar>
        <div class="totalInfo" v-show="!addFlag">
            <div class="mainContentFluid appointmentTop">
                <div class="fluidWidth">
                    <yd-cell-group>
                        <yd-cell-item>
                            <yd-textarea slot="right" placeholder="请输入会议主题..." v-model="titleValue"></yd-textarea>
                        </yd-cell-item>
                    </yd-cell-group>
                    <div class="select">
                        <div class="selectLeft">
                            会议类别
                        </div>
                        <div class="selectRight" v-on:click="editFlag ? noClick() : statusPopup = true">
                            <div class="typeSelect" v-text="typeValue==''?'请选择':typeValue">请选择</div>
                            <router-link to="#" slot="right">
                                <yd-navbar-next-icon></yd-navbar-next-icon>
                            </router-link>
                        </div>
                    </div>
                </div>
            </div>
            <div class="mainContentFluid startAndEnd">
                <div class="fluidWidth">
                    <yd-flexbox>
                        <div class="icon"><img src="../../../static/images/conference/1.png"></div>
                            <yd-flexbox-item>开始</yd-flexbox-item>
                            <yd-datetime class="starTime" :start-date="currentdate" :end-date="threeWeek" type="datetime" v-model="startTime"></yd-datetime>
                    </yd-flexbox>
                    <yd-flexbox>
                        <yd-flexbox-item>结束</yd-flexbox-item>
                        <yd-datetime class="starTime" type="time" v-model="endTime"></yd-datetime>
                    </yd-flexbox>
                </div>
            </div>
            <div class="mainContentFluid startAndEnd">
                <div class="fluidWidth">
                    <yd-flexbox>
                        <div class="icon"><img src="../../../static/images/conference/3.png"></div>
                            <yd-flexbox-item>会议地点</yd-flexbox-item>
                            <div v-on:click="editFlag ? noClick() : locationPopup = true;" class="locationSelect" v-text="addressValue==''?'请选择':addressValue">请选择</div>
                            <div class="nextIcon">
                                <router-link to="#" slot="right">
                                    <yd-navbar-next-icon></yd-navbar-next-icon>
                                </router-link>
                            </div>
                    </yd-flexbox>
                    <yd-flexbox>
                        <yd-flexbox-item class="meetroom">会议室</yd-flexbox-item>
                        <div v-on:click="$dialog.toast({mes: '请先选择会议地点', timeout: 2000})" class="meetingSelect" v-show="addressValue == ''">请选择</div>
                        <div v-on:click="editFlag ? noClick() : meetingPopup = true" v-show="addressValue != ''" class="meetingSelect" v-text="roomValue==''?'请选择':roomValue">请选择</div>
                        <div class="nextIcon">
                            <router-link to="#" slot="right">
                                <yd-navbar-next-icon></yd-navbar-next-icon>
                            </router-link>
                        </div>
                    </yd-flexbox>
                </div>
            </div>
            <div class="mainContentFluid selectMeeting">
                <div class="fluidWidth">
                    <yd-flexbox>
                        <div class="icon"><img src="../../../static/images/conference/4.png"></div>
                            <yd-flexbox-item>参会人</yd-flexbox-item>
                            <div class="add" @click.stop.prevent="addFlag = true;firstFlag=true">添加</div>
                    </yd-flexbox>
                </div>
            </div>
            <div class="mainContentFluid selectMeeting per" v-show="ppflag&&participatorArr.length>0">
                <div class="fluidWidth per">
                    <div v-for="(value, index) in participatorArr" class="list_item" :key="index">
                        <span v-text="value" class="attendName"></span>
                        <img class="meetImg" src="../../../static/images/conference/12.png" @click.stop.prevent="Deletemp(participatorArr,index)">
                    </div>
                    </div>
                </div>
                <div class="mainContentFluid selectMeeting">
                    <div class="fluidWidth">
                        <yd-flexbox>
                            <div class="icon"><img src="../../../static/images/conference/2.png"></div>
                                <yd-flexbox-item>会前提醒</yd-flexbox-item>
                                <div v-on:click="timePopup = true" class="timingSelect" v-text="timeValue == ''?'请选择':'提前'+timeValue+'分钟'">请选择</div>
                                <div class="nextIcon">
                                    <router-link to="#" slot="right">
                                        <yd-navbar-next-icon></yd-navbar-next-icon>
                                    </router-link>
                                </div>
                        </yd-flexbox>
                    </div>
                </div>
                <div class="mainContentFluid selectMeeting" v-show="flag">
                    <div class="fluidWidth moreSelect">
                        <yd-flexbox>
                            <div class="icon"><img src="../../../static/images/conference/7.png"></div>
                                <yd-flexbox-item>投影设备</yd-flexbox-item>
                                <div class="projectionSelect" v-text="tyValue == 1?'有':'无'">请选择</div>
                                <div class="nextIcon">
                                    <router-link to="#" slot="right">
                                        <yd-navbar-next-icon></yd-navbar-next-icon>
                                    </router-link>
                                </div>
                        </yd-flexbox>
                    </div>
                    <div class="fluidWidth moreSelect">
                        <yd-flexbox>
                            <div class="icon"><img src="../../../static/images/conference/9.png"></div>
                                <yd-flexbox-item>视频</yd-flexbox-item>
                                <div class="videoSelect" v-text="videoValue == 1?'有':'无'">请选择</div>
                                <div class="nextIcon">
                                    <router-link to="#" slot="right">
                                        <yd-navbar-next-icon></yd-navbar-next-icon>
                                    </router-link>
                                </div>
                        </yd-flexbox>
                    </div>
                    <div class="fluidWidth moreSelect">
                        <yd-flexbox>
                            <div class="icon"><img src="../../../static/images/conference/8.png"></div>
                                <yd-flexbox-item>茶水</yd-flexbox-item>
                                <div v-on:click="teawaterPopup = true" class="teawaterSelect" v-text="teaValue == 1?'需要':'不需要'">请选择</div>
                                <div class="nextIcon">
                                    <router-link to="#" slot="right">
                                        <yd-navbar-next-icon></yd-navbar-next-icon>
                                    </router-link>
                                </div>
                        </yd-flexbox>
                    </div>
                    <div class="fluidWidth moreSelect">
                        <yd-flexbox>
                            <div class="icon"><img src="../../../static/images/conference/10.png"></div>
                                <yd-flexbox-item>双流播放</yd-flexbox-item>
                                <div v-on:click="dualPlayPopup = true" class="dualPlaySelect" v-text="dualValue == 1?'需要':'不需要'">请选择</div>
                                <div class="nextIcon">
                                    <router-link to="#" slot="right">
                                        <yd-navbar-next-icon></yd-navbar-next-icon>
                                    </router-link>
                                </div>
                        </yd-flexbox>
                    </div>
                    <div class="fluidWidth">
                        <yd-flexbox>
                            <div class="icon"><img src="../../../static/images/conference/11.png"></div>
                                <yd-flexbox-item>视频录播</yd-flexbox-item>
                                <div v-on:click="videoRecordingPopup = true" class="videoRecordingSelect" v-text="vrValue == 1?'需要':'不需要'">请选择</div>
                                <div class="nextIcon">
                                    <router-link to="#" slot="right">
                                        <yd-navbar-next-icon></yd-navbar-next-icon>
                                    </router-link>
                                </div>
                        </yd-flexbox>
                    </div>
                </div>
                <div class="mainContentFluid appointmentTop" v-show="flag">
                    <div class="fluidWidth bottomInput">
                        <div class="otherRequire">其他要求</div>
                        <yd-cell-group>
                            <yd-cell-item>
                                <yd-textarea slot="right" placeholder="请填写其他要求..." v-model="otherValue"></yd-textarea>
                            </yd-cell-item>
                        </yd-cell-group>
                    </div>
                </div>
                <div class="mainContent bottomBtn">
                    <yd-button class="button" v-if="flag == false" size="large" type="primary" bgcolor="#00B6FC" color="#fefefe" @click.native="order">确定</yd-button>
                    <yd-button class="button" v-else size="large" type="primary" bgcolor="#00B6FC" color="#fefefe" @click.native="updateOrder">确定</yd-button>
                    <!-- <yd-button class="button" size="large" type="primary" bgcolor="#ff536f" color="#fefefe" @click.native="orderRecord">会议室预定记录</yd-button> -->
                </div>
                <yd-popup v-model="statusPopup" position="bottom" height="4rem">
                    <div class="confirmBtn" slot="top">
                        <div class="leftBtn" v-on:click="statusPopup = false">取消</div>
                        <div class="rightBtn" v-on:click="statusPopup = false;conferenceType()">确定</div>
                    </div>
                    <div class="selectStatus conferenceType">
                        <ul class="ul">
                            <li class="li" v-bind:class="{active:meetingType==1}" v-on:click="meetingType=1;typeValue='部门会议'">部门会议</li>
                            <li class="li" v-bind:class="{active:meetingType==2}" v-on:click="meetingType=2;typeValue='公司会议'">公司会议</li>
                            <li class="li" v-bind:class="{active:meetingType==3}" v-on:click="meetingType=3;typeValue='分公司会议'">分公司会议</li>
                            <li class="li" v-bind:class="{active:meetingType==4}" v-on:click="meetingType=4;typeValue='总裁办公会'">总裁办公会</li>
                        </ul>
                    </div>
                </yd-popup>
                <yd-popup v-model="locationPopup" position="bottom" height="4rem">
                    <div class="confirmBtn" slot="top">
                        <div class="leftBtn" v-on:click="locationPopup = false">取消</div>
                        <div class="rightBtn" v-on:click="locationPopup = false;selectClose = true;location()">确定</div>
                    </div>
                    <div class="selectStatus location">
                        <ul class="ul" v-for="(item, index) in meetGroup" :key="index">
                            <li class="li" v-bind:class="{active:locateType==index}" v-on:click="meetAddressId=item.id;getMeetingRoom();locateType=index;addressValue=item.buildnm;roomValue='';statusType=-1;" v-text="item.buildnm"></li>
                        </ul>
                    </div>
                </yd-popup>
                <yd-popup v-model="meetingPopup" position="bottom" height="4rem">
                    <div class="confirmBtn" slot="top">
                        <div class="leftBtn" v-on:click="meetingPopup = false">取消</div>
                        <div class="rightBtn" v-on:click="meetingPopup = false;selectClose = true;meeting()">确定</div>
                    </div>
                    <div class="selectStatus meeting">
                        <ul class="ul" v-for="(value, index) in meetRoom" :key="index">
                            <li class="li" v-bind:class="{active:statusType==index}" v-on:click="statusType=index;roomValue=value.meetName;roomId=value.id" v-text="value.meetName"></li>
                        </ul>
                    </div>
                </yd-popup>
                <yd-popup v-model="timePopup" position="bottom" height="4rem">
                    <div class="confirmBtn" slot="top">
                        <div class="leftBtn" v-on:click="timePopup = false">取消</div>
                        <div class="rightBtn" v-on:click="timePopup = false;selectClose = true;timing()">确定</div>
                    </div>
                    <div class="selectStatus timing">
                        <ul class="ul">
                            <li class="li" v-bind:class="{active:timeType==1}" v-on:click="timeType=1;timeValue=15" :timeValue="15">提前15分钟</li>
                            <li class="li" v-bind:class="{active:timeType==2}" v-on:click="timeType=2;timeValue=30" :timeValue="30">提前30分钟</li>
                            <li class="li" v-bind:class="{active:timeType==3}" v-on:click="timeType=3;timeValue=60" :timeValue="60">提前60分钟</li>
                        </ul>
                    </div>
                </yd-popup>
                <!-- <yd-popup v-model="projectionPopup" position="bottom" height="4rem">
                <div class="confirmBtn" slot="top">
                    <div class="leftBtn" v-on:click="projectionPopup = false">取消</div>
                    <div class="rightBtn" v-on:click="projectionPopup = false;selectClose = true;projection()">确定</div>
                </div>
                <div class="selectStatus projection">
                    <ul class="ul">
                        <li class="li" v-bind:class="{active:statusType1==1}" v-on:click="statusType1=1;tyValue=1">需要</li>
                        <li class="li" v-bind:class="{active:statusType1==2}" v-on:click="statusType1=2;tyValue=0">不需要</li>
                    </ul>
                </div>
            </yd-popup> -->
                <yd-popup v-model="teawaterPopup" position="bottom" height="4rem">
                    <div class="confirmBtn" slot="top">
                        <div class="leftBtn" v-on:click="teawaterPopup = false">取消</div>
                        <div class="rightBtn" v-on:click="teawaterPopup = false;selectClose = true;teawater()">确定</div>
                    </div>
                    <div class="selectStatus teawater">
                        <ul class="ul">
                            <li class="li" v-bind:class="{active:statusType2==1}" v-on:click="statusType2=1;teaValue=1">需要</li>
                            <li class="li" v-bind:class="{active:statusType2==2}" v-on:click="statusType2=2;teaValue=0">不需要</li>
                        </ul>
                    </div>
                </yd-popup>
                <!-- <yd-popup v-model="videoPopup" position="bottom" height="4rem">
                <div class="confirmBtn" slot="top">
                    <div class="leftBtn" v-on:click="videoPopup = false">取消</div>
                    <div class="rightBtn" v-on:click="videoPopup = false;selectClose = true;video()">确定</div>
                </div>
                <div class="selectStatus video">
                    <ul class="ul">
                        <li class="li" v-bind:class="{active:statusType3==1}" v-on:click="statusType3=1;videoValue=1">需要</li>
                        <li class="li" v-bind:class="{active:statusType3==2}" v-on:click="statusType3=2;videoValue=0">不需要</li>
                    </ul>
                </div>
            </yd-popup> -->
                <yd-popup v-model="dualPlayPopup" position="bottom" height="4rem">
                    <div class="confirmBtn" slot="top">
                        <div class="leftBtn" v-on:click="dualPlayPopup = false">取消</div>
                        <div class="rightBtn" v-on:click="dualPlayPopup = false;selectClose = true;dualPlay()">确定</div>
                    </div>
                    <div class="selectStatus dualPlay">
                        <ul class="ul">
                            <li class="li" v-bind:class="{active:statusType4==1}" v-on:click="statusType4=1;dualValue=1">需要</li>
                            <li class="li" v-bind:class="{active:statusType4==2}" v-on:click="statusType4=2;dualValue=0">不需要</li>
                        </ul>
                    </div>
                </yd-popup>
                <yd-popup v-model="videoRecordingPopup" position="bottom" height="4rem">
                    <div class="confirmBtn" slot="top">
                        <div class="leftBtn" v-on:click="videoRecordingPopup = false">取消</div>
                        <div class="rightBtn" v-on:click="videoRecordingPopup = false;selectClose = true;videoRecording()">确定</div>
                    </div>
                    <div class="selectStatus videoRecording">
                        <ul class="ul">
                            <li class="li" v-bind:class="{active:statusType5==1}" v-on:click="statusType5=1;vrValue=1">需要</li>
                            <li class="li" v-bind:class="{active:statusType5==2}" v-on:click="statusType5=2;vrValue=0">不需要</li>
                        </ul>
                    </div>
                </yd-popup>
            </div>
            <yd-tabbar class="schedules" slot="tabbar" bgcolor="#F4F4F2" activeColor="#369df4" color="#999999" v-show="!addFlag&&statusPopup==false&&locationPopup==false&&meetingPopup==false&&timePopup==false&&projectionPopup==false&&teawaterPopup==false&&videoPopup==false&&dualPlayPopup==false&&videoRecordingPopup==false">
                <yd-tabbar-item link="" title="预约会议室" active v-if="flag == false">
                    <yd-icon name="unStart" slot="icon" size="0.54rem"></yd-icon>
                </yd-tabbar-item>
                <yd-tabbar-item link="" title="预约会议室" active v-else>
                    <yd-icon name="unStart" slot="icon" size="0.54rem"></yd-icon>
                </yd-tabbar-item>
                <yd-tabbar-item title="会议室预定记录" link="" @click.native="orderRecord()">
                    <yd-icon name="unAttend" slot="icon" size="0.54rem"></yd-icon>
                </yd-tabbar-item>
            </yd-tabbar>
    </yd-layout>
</template>
<script>
import trsTree from '../../components/util/trsTree/trsTree.vue';
import { mapGetters, mapMutations } from 'vuex';
import $ from '../../jquery-vendor.js';
export default {
    name: 'appointment',
    components: {
        trsTree
    },
    data() {
        return {
            statusPopup: false,
            selectClose: false,
            meetingPopup: false,
            locationPopup: false,
            timePopup: false,
            projectionPopup: false,
            teawaterPopup: false,
            videoPopup: false,
            dualPlayPopup: false,
            videoRecordingPopup: false,
            meetGroup: [],
            meetRoom: [],
            flag: this.$route.params.flag === 'true' || this.$route.params.flag === true,
            peopleValue: [], // 参会人员
            startTime: '', // 开始时间
            endTime: '', // 结束时间
            timeValue: 15, // 提醒时间
            otherValue: '', // 其他要求
            titleValue: '', // 会议标题
            typeValue: '部门会议', // 会议类别
            addressValue: '', // 会议地点
            roomValue: '', // 会议室名称
            roomId: '', // 会议室ID
            tyValue: '', // 投影设备
            teaValue: 0, // 茶水
            videoValue: '', // 视频
            dualValue: 0, // 双流播放
            vrValue: 0, // 视频录播
            addFlag: false,
            ppflag: false,
            type: 'share', // 树的类型传参
            statusType: -1,
            statusType1: -1,
            statusType2: 2,
            statusType3: -1,
            statusType4: 2,
            statusType5: 2,
            timeType: -1,
            locateType: -1,
            // personFlag: false,
            // personArr: [], // 参会人数组
            personId: '', // 参会人ID
            currentdate: '',
            meetingType: 999,
            participatorArr: [],
            sendEndTime: '',
            personIdArr: [],
            editFlag: false, // 是否是编辑
            Bookingid: '',
            firstFlag: false,
            startHour: 0,
            meetAddressId: 0, // 会议室地点ID
            currentHour: '',
            threeWeek: '' // 三周时间
        }
    },
    mounted: function() {
        let vm = this;
        document.setTitle('预约会议室');
        if (vm.$route.params.meetId != undefined) {
            vm.editMeeting();
        }
        vm.meetLocation();
        // vm.flag === true ? vm.editMeeting() : vm.meetLocation();
        vm.currentdate = vm.formatDate(new Date(), 'yyyy-MM-dd hh:mm');
        vm.startTime = vm.currentdate;
        vm.endTime = vm.formatDate(vm.currentdate, 'hh:mm');
        let weekNum = new Date().getDay();
        let times = new Date().getTime();
        let times2 = '';
        if (weekNum == 0) {
            times2 = times + 14 * 24 * 60 * 60 * 1000;
        } else {
            times2 = times + (14 + (7 - weekNum)) * 24 * 60 * 60 * 1000;
        }
        vm.threeWeek = vm.formatDate(new Date(times2), 'yyyy-MM-dd') + ' 23:59';
        console.log(vm.threeWeek);
    },
    computed: {
        ...mapGetters(['getUserInfo', 'getMeetingInfo'])
    },
    methods: {
        ...mapMutations([
            'setMeetingInfo'
        ]),
        select(activeObj, selectObj) {
            if (activeObj == '') {
                selectObj.text('请选择');
            } else {
                selectObj.text(activeObj);
            }
        },
        conferenceType() {
            let vm = this;
            let activeObj = $('.conferenceType ul li.active').text();
            let selectObj = $('.typeSelect');
            vm.select(activeObj, selectObj);
        },
        location() {
            let vm = this;
            let activeObj = $('.location ul li.active').text();
            let selectObj = $('.locationSelect');
            vm.select(activeObj, selectObj);
        },
        meeting() {
            let vm = this;
            let activeObj = $('.meeting ul li.active').text();
            let selectObj = $('.meetingSelect');
            vm.select(activeObj, selectObj);
        },
        timing() {
            let vm = this;
            let activeObj = $('.timing ul li.active').text();
            let selectObj = $('.timingSelect');
            vm.select(activeObj, selectObj);
        },
        projection() {
            let vm = this;
            let activeObj = $('.projection ul li.active').text();
            let selectObj = $('.projectionSelect');
            vm.select(activeObj, selectObj);
        },
        teawater() {
            let vm = this;
            let activeObj = $('.teawater ul li.active').text();
            let selectObj = $('.teawaterSelect');
            vm.select(activeObj, selectObj);
        },
        video() {
            let vm = this;
            let activeObj = $('.video ul li.active').text();
            let selectObj = $('.videoSelect');
            vm.select(activeObj, selectObj);
        },
        dualPlay() {
            let vm = this;
            let activeObj = $('.dualPlay ul li.active').text();
            let selectObj = $('.dualPlaySelect');
            vm.select(activeObj, selectObj);
        },
        videoRecording() {
            let vm = this;
            let activeObj = $('.videoRecording ul li.active').text();
            let selectObj = $('.videoRecordingSelect');
            vm.select(activeObj, selectObj);
        },
        // 获取会议地点及对应会议室
        meetLocation() {
            let vm = this;
            vm.get(vm.conf.portalMobile + '/API/MeetManager/meet/bulid', {
                'name': vm.getUserInfo.ssotoken
            }, function(res) {
                vm.meetGroup = res;
            })
        },
        // 从会议详情页面带过来的详细信息
        editMeeting() {
            let vm = this;
            vm.editFlag = true;
            vm.ppflag = true;
            vm.get(vm.conf.portalMobile + '/API/MeetManager/meet/preview', {
                'name': vm.getUserInfo.ssotoken,
                'mid': vm.$route.params.meetId
            }, function(res) {
                if (res.meetPeopleIds != '') {
                    vm.personIdArr = res.meetPeopleIds.split(',');
                    vm.personId = vm.personIdArr.join();
                    vm.participatorArr = res.meetPeoples.split(',');
                }
                // vm.personIdArr = res.meetPeopleIds != '' ? vm.replaceNone(res.meetPeopleIds.split(',')) : res.meetPeopleIds;
                // vm.participatorArr = res.meetPeoples != '' ? vm.replaceNone(res.meetPeoples.split(',')) : res.meetPeoples;
                vm.startTime = res.startTime;
                vm.endTime = vm.formatDate(res.endTime, 'hh:mm');
                vm.timeValue = res.Bookingid.remind;
                if (vm.timeValue == '15') {
                    vm.timeType = 1;
                } else if (vm.timeValue == '30') {
                    vm.timeType = 2;
                } else if (vm.timeValue == '60') {
                    vm.timeType = 3;
                }
                vm.otherValue = res.Bookingid.remark1;
                vm.titleValue = res.meetTitle;
                vm.typeValue = res.meetType;
                vm.addressValue = res.meetAddress;
                vm.roomValue = res.roomRoomName;
                vm.roomId = res.roomRoomId;
                vm.tyValue = res.Bookingid.projectorflg;
                vm.teaValue = res.Bookingid.teaflg;
                vm.Bookingid = res.Bookingid.id;
                if (vm.teaValue == '1') {
                    vm.statusType2 = 1;
                }
                vm.videoValue = res.Bookingid.videoflg;
                vm.dualValue = res.Bookingid.shuangliu;
                if (vm.dualValue == '1') {
                    vm.statusType4 = 1;
                }
                vm.vrValue = res.Bookingid.videolubo;
                if (vm.vrValue == '1') {
                    vm.statusType5 = 1;
                }
            })
        },
        // 第一次填写会议信息
        order() {
            let vm = this;
            let date = new Date(vm.currentdate);
            let date2 = new Date(vm.startTime);
            vm.sendEndTime = vm.formatDate(vm.startTime, 'yyyy-MM-dd') + ' ' + vm.endTime;
            let date3 = new Date(vm.sendEndTime);
            if (vm.titleValue == '') {
                vm.$dialog.toast({
                    mes: '请输入会议主题',
                    timeout: 2000
                });
            } else if (vm.typeValue == '') {
                vm.$dialog.toast({
                    mes: '请选择会议类别',
                    timeout: 2000
                });
            } else if (date.getTime() > date2.getTime()) {
                vm.$dialog.toast({
                    mes: '开始时间小于当前时间',
                    timeout: 2000
                });
            } else if (date2.getTime() >= date3.getTime()) {
                vm.$dialog.toast({
                    mes: '结束时间小于或等于开始时间',
                    timeout: 2000
                });
            } else if (vm.addressValue == '') {
                vm.$dialog.toast({
                    mes: '请选择会议地点',
                    timeout: 2000
                });
            } else if (vm.roomValue == '') {
                vm.$dialog.toast({
                    mes: '请选择会议室',
                    timeout: 2000
                });
            } else if (vm.participatorArr.length == 0) {
                vm.$dialog.toast({
                    mes: '请选择参会人',
                    timeout: 2000
                });
            } else {
                vm.get(vm.conf.portalMobile + '/API/MeetManager/meet/judgeMeetingTime.action', {
                    'meetAddress': vm.roomId,
                    'startTime': vm.startTime,
                    'endTime': vm.sendEndTime,
                    'meetId': '',
                    'name': vm.getUserInfo.ssotoken
                }, function(res) {
                    if (res.jsonData == true) {
                        vm.$dialog.toast({
                            mes: '会议室已被预约',
                            timeout: 2000
                        });
                    } else {
                        vm.post(vm.conf.portalMobile + '/API/MeetManager/meet/save', {
                            'flag': 'mobile',
                            'name': vm.getUserInfo.ssotoken,
                            'meetUsers': 4,
                            'roomid': vm.roomId,
                            'meetBeginTime': vm.startTime,
                            'meetEndTime': vm.sendEndTime,
                            'maddress': vm.addressValue + vm.roomValue,
                            'meTitle': vm.titleValue,
                            'meetsType': vm.typeValue,
                            'meetperson': vm.personId,
                            'roomBook.remind': vm.timeValue,
                            'roomBook.teaflg': vm.teaValue,
                            'roomBook.shuangliu': vm.dualValue,
                            'roomBook.videolubo': vm.videoValue,
                            'roomBook.remark1': vm.otherValue
                        }, function(res) {
                            vm.$dialog.toast({
                                mes: '会议室预约成功',
                                timeout: 2000,
                                callback: () => {
                                    vm.$router.push({
                                        name: 'resHistory',
                                        params: {
                                            label: '1'
                                        }
                                    });
                                }
                            });
                        });
                    }
                });
            }
        },
        // share() {
        //     let vm = this;
        //     vm.get(vm.conf.uchatMessage + '/sendMessageApi', {
        //         'token': `${vm.getUserInfo.ssotoken}L/wp-2q${vm.getUserInfo.ssotokenCode}`,
        //         'title': '会议提醒',
        //         'url': 'http://172.18.44.44/meetingManage',
        //         'empcode': vm.personId,
        //         'summary': '会议主题：' + vm.titleValue + '\n' + '\n' + '会议时间：' + vm.startTime + '-' + vm.endTime + '\n' + '\n' + '会议地点：' + vm.addressValue + vm.roomValue
        //     }, function (res) {})
        // },
        // 修改后提交
        updateOrder() {
            let vm = this;
            console.log(vm.teaValue);
            vm.sendEndTime = vm.formatDate(vm.startTime, 'yyyy-MM-dd') + ' ' + vm.endTime;
            console.log(vm.sendEndTime);
            let date = new Date(vm.currentdate);
            let date2 = new Date(vm.startTime);
            let date3 = new Date(vm.sendEndTime);
            if (vm.titleValue == '') {
                vm.$dialog.toast({
                    mes: '请输入会议主题',
                    timeout: 2000
                });
            } else if (date.getTime() > date2.getTime()) {
                vm.$dialog.toast({
                    mes: '开始时间小于当前时间',
                    timeout: 2000
                });
            } else if (date2.getTime() >= date3.getTime()) {
                vm.$dialog.toast({
                    mes: '结束时间小于或等于开始时间',
                    timeout: 2000
                });
            } else if (vm.participatorArr.length == 0) {
                vm.$dialog.toast({
                    mes: '请选择参会人',
                    timeout: 2000
                });
            } else {
                vm.get(vm.conf.portalMobile + '/API/MeetManager/meet/judgeMeetingTime.action', {
                    'meetAddress': vm.roomId,
                    'startTime': vm.startTime,
                    'endTime': vm.sendEndTime,
                    'meetId': vm.Bookingid,
                    'name': vm.getUserInfo.ssotoken
                }, function(res) {
                    if (res.jsonData == true) {
                        vm.$dialog.toast({
                            mes: '会议室已被预约',
                            timeout: 2000
                        });
                    } else {
                        vm.post(vm.conf.portalMobile + '/API/MeetManager/meet/update', {
                            'flag': 'mobile',
                            'name': vm.getUserInfo.ssotoken,
                            'meetUsers': 4,
                            'id': vm.$route.params.meetId,
                            'meetBeginTime': vm.startTime,
                            'meetEndTime': vm.sendEndTime,
                            'maddress': vm.addressValue + vm.roomValue,
                            'meTitle': vm.titleValue,
                            'meetsType': vm.typeValue,
                            'meetperson': vm.personId,
                            'roomBook.remind': vm.timeValue,
                            'roomBook.teaflg': vm.teaValue,
                            'roomBook.shuangliu': vm.dualValue,
                            'roomBook.videolubo': vm.vrValue,
                            'roomBook.remark1': vm.otherValue
                        }, function(res) {
                            vm.$dialog.toast({
                                mes: '会议室修改成功',
                                timeout: 2000,
                                callback: () => {
                                    vm.$router.push('/resHistory');
                                }
                            });
                        });
                    }
                });
            }
        },
        // 查看已预订的会议信息
        orderRecord() {
            console.log(1);
            let vm = this;
            vm.$router.push({
                name: 'resHistory',
                params: {
                    label: '1'
                }
            })
        },
        // 根据已选中的会议地点查询对应子会议室
        getMeetRoom(index) {
            let vm = this;
            vm.meetRoom = this.meetGroup[index].meetingRooms;
        },
        addFocus(json) {
            console.log(json);
            let vm = this;
            if (vm.participatorArr.length == 0) {
                for (let key in json) {
                    vm.personIdArr.push(key);
                    for (let key1 in json[key]) {
                        vm.participatorArr.push(key1)
                        for (let i = 0; i < vm.personIdArr.length; i++) {
                            if (vm.personIdArr[i] == key && json[key][key1] == 0) {
                                vm.personIdArr.splice(i, 1);
                                vm.participatorArr.splice(i, 1);
                            }
                        }
                    }
                }
            } else {
                for (let keyId in json) {
                    vm.personIdArr.push(keyId);
                    vm.personIdArr = Array.from(new Set(vm.personIdArr));
                    for (let key1 in json[keyId]) {
                        for (let i = 0; i < vm.personIdArr.length; i++) {
                            if (vm.personIdArr[i] == keyId && json[keyId][key1] == 0) {
                                vm.personIdArr.splice(i, 1);
                            }
                        }
                    }
                }
                for (let key in json) {
                    for (let key2 in json[key]) {
                        for (let i = 0; i < vm.participatorArr.length; i++) {
                            if (vm.participatorArr[i] == key2 && json[key][key2] == 0) {
                                vm.participatorArr.splice(i, 1);
                            } else if (vm.participatorArr[i] != key2 && json[key][key2] == 1) {
                                vm.participatorArr.push(key2);
                                vm.participatorArr = Array.from(new Set(vm.participatorArr));
                            }
                        }
                    }
                }
            }
            vm.personId = vm.personIdArr.join(',');
            vm.ppflag = true;
            console.log(vm.participatorArr);
            console.log(vm.personIdArr);
        },
        // 组织树点击确定
        treeSure(personObj) {
            let vm = this;
            vm.addFlag = !vm.addFlag;
            vm.setMeetingInfo({
                meetPeople: vm.personId
            });
        },
        replaceNone(arr) {
            for (var i = 0; i < arr.length; i++) {
                if (arr[i] == '') {
                    arr.splice(i, 1);
                }
                return arr;
            }
        },
        Deletemp(arr, index) {
            let vm = this;
            arr.splice(index, 1);
            vm.personIdArr.splice(index, 1);
            if (arr.length == 0) {
                vm.ppflag = false;
            }
            vm.personId = vm.personIdArr.join();
            console.log(vm.participatorArr);
            console.log(vm.personIdArr);
        },
        // 不能编辑
        noClick() {
            let inFocus = document.activeElement;
            inFocus.blur()
            this.$dialog.toast({
                mes: '该内容不能被编辑',
                timeout: 1000
            });
        },
        getMeetingRoom() {
            let vm = this;
            let etime = vm.formatDate(vm.startTime, 'yyyy-MM-dd') + ' ' + vm.endTime;
            vm.get(vm.conf.portalMobile + '/API/MeetManager/meet/listBookroomByTime', {
                'stime': vm.startTime,
                'etime': etime,
                'buildid': vm.meetAddressId,
                'name': vm.getUserInfo.ssotoken
            }, function(res) {
                vm.meetRoom = res.jsonData.data;
                console.log(vm.meetRoom);
            });
        }
    }
}
</script>
<style lang="less" scoped>
.button {
    height: 1rem;
    margin-bottom: .17rem;
}

.submit {
    background-color: #fff;
    border-bottom: 1px dashed #c1baba;
    line-height: 1rem;
    text-align: center;
    font-size: .26rem;
}

.list_item {
    width: 25%;
    float: left;
}

.per {
    height: auto;
    padding-bottom: 0;
}

.icon {
    padding-right: 0.26rem;
}

.icon img {
    width: 0.29rem;
    height: 0.29rem;
    margin-top: 0.07rem;
}

.starTime {
    color: #369df4;
}

.add {
    padding-right: 0.45rem;
    color: #369df4;
}

.moreSelect {
    border-bottom: 1px solid #dcdcdc;
}

.bottomInput .yd-cell-item {
    padding-left: 0;
    border-bottom: none;
}

.otherRequire {
    font-size: 0.3rem;
    color: #333;
}

.yd-datetime-input {
    width: auto;
}

.meetroom {
    margin-left: 0.05rem;
}

.meetImg {
    line-height: .3rem;
    height: .3rem;
    padding-top: .08rem;
    width: .4rem;
}

.yd-tabbar {
    padding: 0;
}

.yd-tabbar:after {
    border-top: none;
}

.yd-btn-block {
    height: 1rem;
    font-size: 0.3rem !important;
    margin-top: 0;
    border-radius: 0;
}

.attendName {
    font-size: .3rem;
}

.appointmentTop:first-child {
    margin-top: 0;
}

.bottomBtn {
    margin-top: .2rem;
}

.yd-icon-unStart:before,
.yd-icon-unAttend:before,
.yd-icon-start:before,
.yd-icon-attend:before {
    display: block;
    content: '';
}

.schedules {
    .yd-icon-unStart:before {
        width: .41rem;
        height: .41rem;
        background: url(../../../static/images/booking2@2x.png) no-repeat 0 0;
        background-size: .4rem;
    }

    .yd-icon-unAttend:before {
        width: .41rem;
        height: .41rem;
        background: url(../../../static/images/booking@2x.png) no-repeat 0 0;
        background-size: .4rem;
    }

    .yd-tabbar-txt {
        color: #59a5fd;
    }
}
</style>


